class TutorialComponent {
    constructor(router, trackingService) {
        this.router = router;
        this.trackingService = trackingService;
        this.nextPage = new EventEmitter();
        this.previousPage = new EventEmitter();
        this.buttonNext = true;
    }
    ngAfterViewInit() {
        this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'tutorial' });
    }
    nextStep() {
        if (this.bperData?.buttonNext) {
            this.nextPage.emit();
        }
        else {
            this.router.navigate([{ outlets: { 'signature': ['documentazione'] } }]);
        }
    }
    previousStep() {
        this.previousPage.emit();
    }
}
