class ModalPDFDirective {
    constructor(element, canvasGeneratorService) {
        this.element = element;
        this.canvasGeneratorService = canvasGeneratorService;
        this.container = this.element.nativeElement;
    }
    /**
     * Listen for scroll event and call load function when penultimate page on top/bottom is reached to show/load new page
     */
    onScroll(event) {
        const scrollY = this.container.scrollTop;
        const viewport = this.container.clientHeight;
        this.containerHeight = 0;
        this.clientHeight = 0;
        const children = this.container.children.item(0).children;
        if (this.clientHeight == 0)
            this.clientHeight = children.item(0).clientHeight;
        for (let i = 0; i < children.length; i++)
            this.containerHeight += children.item(i).clientHeight;
        // ! START - DEBUG
        // console.warn('viewport', viewport);
        // console.warn('scrollY', scrollY);
        // console.warn('children.item', children.item(this.canvasGeneratorService.indexCleanerBottom - 1)!.clientHeight);
        // ! END - DEBUG
        if ((viewport + scrollY) >= (this.containerHeight - (children.item(this.canvasGeneratorService.indexCleanerBottom - 1).clientHeight / 2))) {
            this.canvasGeneratorService.loadNext();
        }
        if (scrollY <= children.item(this.canvasGeneratorService.indexCleanerTop).clientHeight) {
            this.canvasGeneratorService.loadPrevious();
        }
    }
}
