class EmissioneCertificatoComponent {
    constructor(emissionService, router, firmaConfigService, errorHandlerService, loaderService, trackingService) {
        this.emissionService = emissionService;
        this.router = router;
        this.firmaConfigService = firmaConfigService;
        this.errorHandlerService = errorHandlerService;
        this.loaderService = loaderService;
        this.trackingService = trackingService;
        this.nextPage = new EventEmitter();
        this.previousPage = new EventEmitter();
        this.view = new EventEmitter();
        this.checked = false;
        this.disable = true;
        this.buttonNext = true;
        this.buttonPrevious = true;
        this.buttonCheck = true;
    }
    /**
     * Subscribes to an observable to manage the different template to show during sign process
     */
    ngOnInit() {
        this.emissionService.isChecked$
            .pipe(catchError(internalError => {
            return throwError(this.errorHandlerService.throwToInternalErrorPage(internalError));
        }))
            .subscribe(value => {
            this.checked = value;
        });
    }
    ngAfterViewInit() {
        if (!this.checked) {
            this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'emissione_certificato' });
        }
        else {
            this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'emissione_certificato:consensi_prestati' });
        }
    }
    viewPdf() {
        if (this.bperData?.buttonCheck) {
            this.view.emit();
        }
        else {
            this.router.navigate([{ outlets: { 'signature': ['richiesta'] } }]);
        }
    }
    enableContinue() {
        this.disable = !this.disable;
    }
    /**
     * Calls API to send acceptance value and to trigger the send of the OTP code
     */
    nextStep() {
        if (this.bperData?.buttonNext) {
            this.nextPage.emit();
        }
        else {
            this.loaderService.show();
            const request = {
                valoreAccettazione: this.checked
            };
            this.acceptSub = this.firmaConfigService.emissioneCertificatoDataSource.concentAcceptance(this.firmaConfigService.bperData.idPratica, request)
                .pipe(catchError((err) => {
                return throwError(this.errorHandlerService.throwToErrorPage(err, 'emissione'));
            }))
                .subscribe((response) => {
                this.firmaConfigService.otpCode = response.idTransazioneOtp;
                this.loaderService.hide();
                this.router.navigate([{ outlets: { 'signature': ['inserimento'] } }]);
            });
        }
    }
    previousStep() {
        this.emissionService.checkDone(false);
        if (this.bperData?.buttonPrevious) {
            this.previousPage.emit();
        }
        else {
            this.router.navigate([{ outlets: { 'signature': ['conferma'] } }]);
        }
    }
    ngOnDestroy() {
        if (this.acceptSub)
            this.acceptSub.unsubscribe();
        this.emissionService.checkDone(false);
    }
}
