class ConfermaPuntiComponent {
    constructor(router, trackingService) {
        this.router = router;
        this.trackingService = trackingService;
        this.nextPage = new EventEmitter();
        this.previousPage = new EventEmitter();
        this.buttonNext = true;
        this.buttonPrevious = true;
    }
    ngAfterViewInit() {
        this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'disclaimer_firma' });
    }
    nextStep() {
        if (this.bperData?.buttonNext) {
            this.nextPage.emit();
        }
        else {
            this.router.navigate([{ outlets: { 'signature': ['emissione'] } }]);
        }
    }
    previousStep() {
        if (this.bperData?.buttonPrevious) {
            this.previousPage.emit();
        }
        else {
            this.router.navigate([{ outlets: { 'signature': ['documentazione'] } }]);
        }
    }
}
