class ConfermaProcessoComponent {
    constructor(dialog, firmaConfigService, errorHandlerService, trackingService) {
        this.dialog = dialog;
        this.firmaConfigService = firmaConfigService;
        this.errorHandlerService = errorHandlerService;
        this.trackingService = trackingService;
        this.nextPage = new EventEmitter();
        this.previousPage = new EventEmitter();
        this.numMaxRequest = 15; // ? Max number of polling requests before timeout
        this.requestSpan = 2000; // ? Time between each polling request
        this.isLoading = true; // ? This is used in the template to show the loading spinner
        this.numRetry = 0;
        if (this.firmaConfigService.bperData) {
            this.processOrcastratorData(this.firmaConfigService.bperData, this.bperData);
        }
    }
    ngOnInit() { this.startPolling(); }
    ngAfterViewInit() {
        this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'thank_you_page:loading' });
    }
    processOrcastratorData(remoteBperData, localBperData) {
        this.bperData = remoteBperData;
        this.requestSpan = remoteBperData.pollingInterval ? remoteBperData.pollingInterval : this.requestSpan;
        this.numMaxRequest = remoteBperData.pollingMaxRetryBeforeTimeout ? remoteBperData.pollingMaxRetryBeforeTimeout : this.numMaxRequest;
    }
    /**
     * Calls API to check the status of sign process every seconds.
     * When it is done, the modal is closed.
     * Redirects to error page after some attempts if the sign process is not complete yet
     */
    startPolling() {
        const pollingInterval = setInterval(() => {
            this.numRetry++;
            const tempPub = this.firmaConfigService.confermaProcessoDataSource.getSignStatus(this.firmaConfigService.bperData.idPratica).subscribe({
                next: (response) => {
                    switch (response.stato) {
                        case 'FIRMATO':
                            this.isLoading = false;
                            this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'thank_you_page:success' });
                            tempPub.unsubscribe();
                            clearInterval(pollingInterval);
                            // ? Close the modal and send a success messsage
                            setTimeout(() => this.dialog.getDialogById("lib-feq")?.close({ event: 'signComplete', res: true }), this.firmaConfigService.bperData.millisecondsToClose);
                            break;
                        case 'IN_ATTESA_DI_FIRMA':
                            if (this.numRetry === this.numMaxRequest) {
                                this.errorMaxRequest = {
                                    message: "Max request reached",
                                    buttonNext: true
                                };
                                tempPub.unsubscribe();
                                clearInterval(pollingInterval);
                                this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'thank_you_page:esito_ko' });
                                throwError(this.errorHandlerService.throwToErrorPage(this.errorMaxRequest, 'inserimento'));
                            }
                            break;
                        default:
                            if (this.numRetry === this.numMaxRequest) {
                                this.errorMaxRequest = {
                                    message: "Timeout reached",
                                    buttonNext: true
                                };
                                tempPub.unsubscribe();
                                clearInterval(pollingInterval);
                                this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'thank_you_page:timeout' });
                                throwError(this.errorHandlerService.throwToErrorPage(this.errorMaxRequest, 'inserimento'));
                            }
                            break;
                    }
                },
                error: (error) => {
                    if (this.numRetry === this.numMaxRequest) {
                        this.errorMaxRequest = {
                            message: "Max requests reached",
                            buttonNext: false
                        };
                        clearInterval(pollingInterval);
                        this.trackingService.emitTrackingEvent({ action: 'NAVIGATION', page: 'thank_you_page:servizio_non_disponibile' });
                        throwError(this.errorHandlerService.throwToErrorPage(this.errorMaxRequest, 'inserimento'));
                    }
                },
                // !! complete: () => tempPub.unsubscribe()
            });
        }, this.requestSpan);
    }
}
