class ErrorsComponent {
    constructor(router, activedRoute, modalService, errorHandler) {
        this.router = router;
        this.activedRoute = activedRoute;
        this.modalService = modalService;
        this.errorHandler = errorHandler;
        // ? Public Outputs
        this.nextPage = new EventEmitter();
        this.buttonNext = true;
        this.titleError = "Questo servizio tornerà attivo il prima possibile.";
        this.subtitleError = "Ti chiediamo di riprovare";
    }
    ngOnInit() {
        this.titleError = this.errorHandler.errorPayload?.messageTitle || "Questo servizio tornerà attivo il prima possibile.";
        this.subtitleError = this.errorHandler.errorPayload?.message || "Ti chiediamo di riprovare";
    }
    /**
       * Getter for the path to redirect after the user aknowledged the error.
       * Actually get the route from the query params.
       * @returns String
    */
    get pathToRedirect() {
        return this.activedRoute.snapshot.queryParams?.path || undefined;
    }
    /**
         * Getter for extra data to return after the user aknowledged the error.
         * Actually get the payload from the query params.
         * @returns String
      */
    // ! Da rivedere. Specialmente i nomi. Creano confusione.
    get extraData() {
        return this.activedRoute.snapshot.queryParams?.extras || undefined;
    }
    onClose() {
        if (this.pathToRedirect) {
            let extraRouteData = this.extraData ? { queryParams: { requestedView: this.extraData } } : undefined;
            this.router.navigate([{ outlets: { 'signature': [this.pathToRedirect] } }], extraRouteData).then((success) => {
                if (!success)
                    return;
                this.errorHandler.cleanErrorPayload();
            });
        }
        else {
            this.modalService.getDialogById("lib-feq")?.close({ event: 'dossierError', status: 'failure', res: false });
        }
    }
}
