class RichiestaCertificatoDirective {
    constructor(element, canvasGeneratorService) {
        this.element = element;
        this.canvasGeneratorService = canvasGeneratorService;
        this.container = this.element.nativeElement;
    }
    ngOnInit() {
    }
    /**
     * Listen for scroll event and call load function when penultimate page on top/bottom is reached to show/load new page
     */
    onScroll(event) {
        const scrollY = this.container.scrollTop;
        const viewport = this.container.clientHeight;
        this.containerHeight = 0;
        this.clientHeight = 0;
        const children = this.container.children.item(2).children.item(0).children;
        if (this.clientHeight == 0)
            this.clientHeight = children.item(0).clientHeight;
        //Add height of scrollable text
        this.containerHeight += this.container.children.item(0).clientHeight;
        for (var i = 0; i < children.length; i++)
            this.containerHeight += children.item(i).clientHeight;
        if ((viewport + scrollY) >= (this.containerHeight - children.item(this.canvasGeneratorService.indexCleanerBottom - 1).clientHeight)) {
            this.canvasGeneratorService.loadNextPage();
        }
        if (scrollY <= this.container.children.item(0).clientHeight + children.item(this.canvasGeneratorService.indexCleanerTop).clientHeight) {
            this.canvasGeneratorService.loadPrevious();
        }
    }
}
