class BperFirmaService {
    constructor(dialogService, router, errorHandlerService) {
        this.dialogService = dialogService;
        this.router = router;
        this.errorHandlerService = errorHandlerService;
        this.operationResult = new EventEmitter();
    }
    updateOperationResult(newPayload, success = true) {
        if (success) {
            this.operationResult.next(newPayload);
        }
        else {
            this.operationResult.error(newPayload);
        }
        // if (newPayload.res === true) {
        //   this.operationResult.complete();
        // }
    }
    // ? This method is pubblically exposed to the CJ and should only be called once, uppon signature initialization
    /**
     * Start the FEQ-signature process.
     * @param @optional payload Payload to be passed to the FEQ-signature process.
     * @returns Void.
     */
    startSignature(payload) {
        if (this.dialogService.getDialogById("lib-feq")) {
            this.dialogService.getDialogById("lib-feq")?.close();
        }
        // !!! SHOULD NOT BE HERE !!!
        this.errorHandlerService.skipPollingStep = false;
        const dialogRef = this.dialogService.open(OrchestratorComponent, {
            id: "lib-feq",
            hasBackdrop: true,
            disableClose: true,
            panelClass: 'feq-dialog',
            backdropClass: 'feq-backdrop',
        });
        const afterOpened = dialogRef.afterOpened().subscribe({
            next: () => document.body.style.background = `rgba(0, 0, 0, .32)`,
            complete: () => afterOpened.unsubscribe()
        });
        const afterClosed = dialogRef.afterClosed().subscribe({
            next: (result) => {
                document.body.style.background = `none`;
                this.router.navigate([{ outlets: { 'signature': null } }]).then((fulfilled) => {
                    this.operationResult.next(result);
                });
            },
            complete: () => {
                afterClosed.unsubscribe();
            }
        });
    }
    /**
     * Destroies the FEQ-signature process and all it's dependencies.
     * @returns Void.
     */
    cleanSignature() {
        this.operationResult.next(null);
    }
}
